*{margin:0;padding:0;box-sizing:border-box;color:#f4f4f4;-webkit-user-select:text !important;-ms-user-select:text !important;user-select:text !important;-moz-user-select:text !important}*::-webkit-scrollbar{width:6px}*::-webkit-scrollbar-track{background:#0f1a2d}*::-webkit-scrollbar-thumb{background-color:#1c3a6d;opacity:0.5;-webkit-transition:transform .25s ease-in-out,opacity .25s ease-in-out;transition:transform .25s ease-in-out,opacity .25s ease-in-out}*::-webkit-scrollbar-thumb:hover{background-color:#2d5497;opacity:1}*:focus{outline:none}html{background-color:#0b1014;height:calc(100%-70px);min-height:calc(100%-70px)}body{font-family:"Montserrat", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}


.loading-container{width:100%;height:100%;display:flex;align-items:center;justify-content:center;flex-direction:column;transition:transform .25s ease-in-out,opacity .25s ease-in-out}.lds-ellipsis{display:inline-block;position:relative;width:80px;height:80px}.lds-ellipsis div{position:absolute;top:33px;width:13px;height:13px;border-radius:50%;background:#2d9cdb;-webkit-animation-timing-function:cubic-bezier(0, 1, 1, 0);animation-timing-function:cubic-bezier(0, 1, 1, 0)}.lds-ellipsis div:nth-child(1){left:8px;-webkit-animation:lds-ellipsis1 0.6s infinite;animation:lds-ellipsis1 0.6s infinite}.lds-ellipsis div:nth-child(2){left:8px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(3){left:32px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(4){left:56px;-webkit-animation:lds-ellipsis3 0.6s infinite;animation:lds-ellipsis3 0.6s infinite}@-webkit-keyframes lds-ellipsis1{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes lds-ellipsis1{0%{transform:scale(0)}100%{transform:scale(1)}}@-webkit-keyframes lds-ellipsis3{0%{transform:scale(1)}100%{transform:scale(0)}}@keyframes lds-ellipsis3{0%{transform:scale(1)}100%{transform:scale(0)}}@-webkit-keyframes lds-ellipsis2{0%{transform:translate(0, 0)}100%{transform:translate(24px, 0)}}@keyframes lds-ellipsis2{0%{transform:translate(0, 0)}100%{transform:translate(24px, 0)}}

h1{font-size:clamp(36px, 4.5vw, 72px)}h2{font-size:clamp(28px, 3.5vw, 54px)}h3{font-size:clamp(28px, 2.5vw, 48px)}h4{font-size:clamp(22px, 2.2vw, 36px);font-weight:300}h5{font-size:clamp(14px, 2.1vw, 16px);font-weight:300}h6{font-size:clamp(12px, 2vw, 16px);font-weight:500}p{font-size:clamp(13px, 2.2vw, 18px)}a{text-decoration:none;color:#56ccf2}.blue-text{color:#56ccf2}.blue-span{position:relative;z-index:1;-webkit-animation:flow 10s ease-in-out infinite;animation:flow 10s ease-in-out infinite;background:linear-gradient(-60deg, #d6b264, #70da82, #7770e0, #81e986);background-size:300%;-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent}.blue-span:before{z-index:-1;content:"";position:absolute;width:105%;height:50%;background:#191a24;transform:translate(-2.5%, 100%)}@-webkit-keyframes flow{0%{background-position:0 50%}50%{background-position:100% 50%}100%{background-position:0 50%}}@keyframes flow{0%{background-position:0 50%}50%{background-position:100% 50%}100%{background-position:0 50%}}.decorations{transition:transform .25s ease-in-out,opacity .25s ease-in-out;position:absolute}.decorations-front,.decorations-semi-front{z-index:3;-webkit-filter:drop-shadow(-5px 5px 5px rgba(45,156,219,0.1));filter:drop-shadow(-5px 5px 5px rgba(45,156,219,0.1))}.decorations-back{z-index:-1;opacity:0.2}.code-heading{color:inherit}.code-heading::before{content:"< "}.code-heading::after{content:" />"}@media screen and (max-width: 480px){.decorations{transition:transform .25s ease-in-out,opacity .25s ease-in-out}.decorations-semi-front{display:none;opacity:0.5}.decorations-back{z-index:-1;opacity:0.5}.code-heading{transform:scale(0.5)}}.Toastify__toast--dark{background:#191a24;box-shadow:-6px 5px 8px rgba(11,16,20,0.87);font-family:inherit}.Toastify__progress-bar--dark{background:#2d9cdb}

.navbar-container{width:100%;height:60px;display:flex;align-items:center;justify-content:center;flex-direction:row;position:fixed;top:0;padding:10px;overflow:hidden;z-index:99;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px)}.navbar-container:before{content:"";width:120%;height:80px;position:absolute;top:-10px;left:-25px;right:0;bottom:0;box-shadow:inset 0 0 0 200px rgba(11,16,20,0.3);-webkit-filter:blur(15px);filter:blur(15px)}.navbar{width:100%;height:60px;display:flex;flex-direction:row;align-items:center;padding:0 10vw;position:absolute}.navbar>*{margin-right:3vw}.navbar-link h5{transition:transform .25s ease-in-out,opacity .25s ease-in-out;color:#bdbdbd}.navbar-link:hover h5{color:#f4f4f4}.navbar-link-active h5{color:#2d9cdb;font-weight:500}.navbar-link-active:hover h5{color:#2d9cdb}@media screen and (max-width: 480px){.navbar{display:flex;align-items:center;justify-content:center;flex-direction:row}.navbar>*{margin:0 2vw}}

.social-media-container{position:fixed;display:flex;bottom:0;left:2vw;z-index:99}@media screen and (max-width: 480px){.social-media-container{margin:0 0 2vw 2vh}}

.social-media-icon-container{padding:7px 2px;width:44px;height:54px;display:flex;align-items:center;justify-content:center;flex-direction:column;position:relative;transition:transform .25s ease-in-out,opacity .25s ease-in-out}.social-media-icon-container .social-media-icon{display:flex;align-items:center;justify-content:center;flex-direction:column;width:100%;height:100%;position:relative}.social-media-icon-container .social-media-icon-move{position:absolute;display:flex;align-items:center;justify-content:center;flex-direction:column}.social-media-icon-container svg,.social-media-icon-container path{transition:transform .25s ease-in-out,opacity .25s ease-in-out}.social-media-icon-container:hover{transform:translateY(-2px)}.social-media-icon-container:hover svg,.social-media-icon-container:hover path{fill:#56ccf2;-webkit-filter:drop-shadow(0 0 5px rgba(86,204,242,0.25));filter:drop-shadow(0 0 5px rgba(86,204,242,0.25))}.social-media-icon-container svg{transform:scale(0.8);width:30px;height:30px}@media screen and (max-width: 480px){.social-media-icon-container{padding:2px;width:30px;height:40px}.social-media-icon-container svg{transform:scale(0.6);width:30px;height:30px}}

